___CSS_LOADER_EXPORT___.push([module.id, `.card-admin {
    background-color: #2C3639;
    color: #DCD7C9;
    border-radius: 20px;
}

.card-admin-body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    height: 40vh;
    padding: 20px;
}

.card-admin-title {
    margin: 10px 0;
    font-weight: 600;
}

.titleAdmin-card {
    display: flex;
    justify-content: start;
    align-items: center;
    height: 10vh;
    padding: 50px 20px;
    background-color: #2C3639;
    color: #DCD7C9;
    border-radius: 20px;
}

.titleAdmin-card h1 {
    font-size: 35px;
    font-weight: 600;
}

.icon-AdminCard {
    font-size: 60px;
}

.btn-masukAdmin {
    display: inline-block;
    padding: 5px 25px;
    border-radius: 10rem;
    text-transform: uppercase;
    font-size: 1rem;
    letter-spacing: 0.15rem;
    transition: all 0.3s;
    position: relative;
    overflow: hidden;
    z-index: 1;
}

.btn-masukAdmin a {
    text-decoration: none;
    color: #2C3639;
}

.btn-masukAdmin::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #A27B5C;
    border-radius: 10rem;
    z-index: -2;
}

.btn-masukAdmin::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: #3F4E4F;
    transition: all 0.3s;
    border-radius: 10rem;
    z-index: -1;
}

.btn-masukAdmin:hover a {
    color: #fff;
}

.btn-masukAdmin:hover:before {
    width: 100%;
}

.chart-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #2C3639;
    border-radius: 20px;
    height: 55vh;
}

.chartDiv {
    width: 90%;
    height: 90%;
}`, "",{"version":3,"sources":["webpack://./src/styles/dashboard.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;IACzB,cAAc;IACd,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;IACtB,kBAAkB;IAClB,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,cAAc;IACd,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,YAAY;IACZ,kBAAkB;IAClB,yBAAyB;IACzB,cAAc;IACd,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,qBAAqB;IACrB,iBAAiB;IACjB,oBAAoB;IACpB,yBAAyB;IACzB,eAAe;IACf,uBAAuB;IACvB,oBAAoB;IACpB,kBAAkB;IAClB,gBAAgB;IAChB,UAAU;AACd;;AAEA;IACI,qBAAqB;IACrB,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,SAAS;IACT,OAAO;IACP,WAAW;IACX,YAAY;IACZ,yBAAyB;IACzB,oBAAoB;IACpB,WAAW;AACf;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,SAAS;IACT,OAAO;IACP,SAAS;IACT,YAAY;IACZ,yBAAyB;IACzB,oBAAoB;IACpB,oBAAoB;IACpB,WAAW;AACf;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,yBAAyB;IACzB,mBAAmB;IACnB,YAAY;AAChB;;AAEA;IACI,UAAU;IACV,WAAW;AACf","sourcesContent":[".card-admin {\r\n    background-color: #2C3639;\r\n    color: #DCD7C9;\r\n    border-radius: 20px;\r\n}\r\n\r\n.card-admin-body {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    flex-direction: column;\r\n    text-align: center;\r\n    height: 40vh;\r\n    padding: 20px;\r\n}\r\n\r\n.card-admin-title {\r\n    margin: 10px 0;\r\n    font-weight: 600;\r\n}\r\n\r\n.titleAdmin-card {\r\n    display: flex;\r\n    justify-content: start;\r\n    align-items: center;\r\n    height: 10vh;\r\n    padding: 50px 20px;\r\n    background-color: #2C3639;\r\n    color: #DCD7C9;\r\n    border-radius: 20px;\r\n}\r\n\r\n.titleAdmin-card h1 {\r\n    font-size: 35px;\r\n    font-weight: 600;\r\n}\r\n\r\n.icon-AdminCard {\r\n    font-size: 60px;\r\n}\r\n\r\n.btn-masukAdmin {\r\n    display: inline-block;\r\n    padding: 5px 25px;\r\n    border-radius: 10rem;\r\n    text-transform: uppercase;\r\n    font-size: 1rem;\r\n    letter-spacing: 0.15rem;\r\n    tra