___CSS_LOADER_EXPORT___.push([module.id, `/* .contactTitle h1,h4{
    color: #2c3639;
} */

.card-form-container {
  padding: 30px;
  border-radius: 0 10px 10px 0;
}

.btn-submitContact {
  float: right;
  bottom: 0;
  background-color: #dcd7c9;
  color: #2c3639;
  min-width: 150px;
  min-height:50px;
  border-radius: 50px;
  font-weight: 600;
  font-size: 16px;
  border: none;
}

.btn-submitContact:hover {
  background-color: #a8a499;
}

.contact-container {
  margin-top: 150px;
  background: #3F4E4F ;
}

.loader-button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  display: block;
  margin: 15px auto;
  position: relative;
  background: #fff;
  box-shadow: -24px 0 #fff, 24px 0 #fff;
  box-sizing: border-box;
  animation: shadowPulse 0.7s linear infinite;
}

.Contact-Illustration {
  transform: scaleX(-1);
}

@keyframes shadowPulse {
  33% {
    background: #fff;
    box-shadow: -24px 0 #a27b5c, 24px 0 #fff;
  }
  66% {
    background: #a27b5c;
    box-shadow: -24px 0 #fff, 24px 0 #fff;
  }
  100% {
    background: #fff;
    box-shadow: -24px 0 #fff, 24px 0 #a27b5c;
  }
}
