___CSS_LOADER_EXPORT___.push([module.id, `.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #3f4e4f;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}

.wisata_favorit {
  display: flex;
  margin-top: 100px;
  justify-content: center;
}

.wisata_title {
  font-size: 40px;
  font-weight: 800;
  color: #3f4e4f;
  text-align: center;
  margin: 10px;
}

.wisata_container {
  min-height: 100vh;
}

.hotel_container {
  min-height: 100vh;
}

.hotel_favorit {
  display: flex;
  margin-top: 100px;
  justify-content: center;
}

.hotel_title {
  font-size: 40px;
  font-weight: 800;
  color: #3f4e4f;
  text-align: center;
  margin: 10px;
}

.empty_favorit h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #3f4e4f;
  font-size: 25px;
  margin-top: 100px;
  font-weight: 800;
}

a,
a:hover {
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

/* Cards */
.postcard {
  flex-wrap: wrap;
  display: flex;
  box-shadow: 0 4px 21px -12px rgba(0, 0, 0, 0.66);
  border-radius: 10px;
  margin: 0 0 2rem 0;
  overflow: hidden;
  position: relative;
  color: #fff;
}
.postcard.dark {
  background-color: #18151f;
}

.postcard a {
  color: inherit;
}
.postcard h1,
.postcard .h1 {
  margin-bottom: 0.5rem;
  font-weight: 500;
  line-height: 1.2;
}
.postcard .small {
  font-size: 80%;
}
.postcard .postcard__title {
  font-size: 1.75rem;
  margin: 3px;
}
.postcard .postcard__img {
  max-height: 180px;
  width: 100%;
  object-fit: cover;
  position: relative;
}
.postcard .postcard__img_link {
  display: contents;
}
.postcard .postcard__bar {
  width: 50px;
  height: 10px;
  margin: 10px 0;
  border-radius: 5px;
  background-color: #3f4e4f;
  transition: width 0.8s ease;
}
.postcard .postcard__text {
  padding: 1.5rem;
  position: relative;
  display: flex;
  flex-direction: column;
}
.postcard .postcard__preview-txt {
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: justify;
  height: 100%;
}
.postcard .postcard__tagbox {
  display: flex;
  flex-flow: row wrap;
  font-size: 14px;
  margin: 20px 0 0 0;
  padding: 0;
  justify-content: center;
}
.postcard .postcard__tagbox .tag__item {
  display: inline-block;
  background: #3f4e4f;
  border-radius: 3px;
  padding: 2.5px 10px;
  margin: 0 5px 5px 0;
  cursor: default;
  user-select: none;
  transition: background-color 0.3s;
}
.postcard .postcard__tagbox .tag__item:hover {
  background: rgb(180, 137, 20);
}
.postcard:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(-70deg, #2c3639, transparent 50%);
  opacity: 1;
  border-radius: 10px;
}
.postcard:hover .postcard__bar {
  width: 100%;
}
@media screen and (min-width: 769px) {
  .postcard {
    flex-wrap: inherit;
  }
  .postcard .postcard__title {
    font-size: 2rem;
  }
  .postcard .postcard__tagbox {
    justify-content: start;
  }
  .postcard .postcard__img {
    max-width: 300px;
    max-height: 100%;
    transition: transform 0.3s ease;
  }
  .postcard .postcard__text {
    padding: 3rem;
    width: 100%;
  }
  .postcard .media.postcard__text:before {
    content: "";
    position: absolute;
    display: block;
    background: #3f4e4f;
    top: -20%;
    height: 130%;
    width: 55px;
  }
  .postcard:hover .postcard__img {
    transform: scale(1.1);
  }
  .postcard:nth-child(2n + 1) {
    flex-direction: row;
  }
  .postcard:nth-child(2n + 0) {
    flex-direction: row-reverse;
  }
  .postcard:nth-child(2n + 1) .postcard__text::before {
    left: -12px !important;
    transform: rotate(4deg);
  }
  .postcard:nth-child(2n + 0) .postcard__text::before {
    right: -12px !important;
    transform: rotate(-4deg);
  }
}
@media screen and (min-width: 1024px) {
  .postcard__text {
    padding: 2rem 3.5rem;
  }
  .postcard__text:before {
    content: "";
    position: absolute;
    display: block;
    top: -20%;
    height: 130%;
    width: 55px;
  }
  .postcard.dark .postcard__text:before {
    background: #18151f;
  }
  .postcard.light .postcard__text:before {
    background: #e1e5ea;
  }
}

/* COLORS */
.postcard .postcard__tagbox .blue.play:hover {
  background: #a27b5c;
}
.blue .postcard__title:hover {
  color: #a27b5c;
}
.blue .postcard__bar {
  background-color: rgb(189, 139, 0);
}
.blue::before {
  background-image: linear-gradient(-30deg, #2c3639, transparent 50%);
}
.blue:nth-child(2n)::before {
  background-image: linear-gradient(30deg, #3f4e4f, transparent 50%);
}

@media screen and (min-width: 769px) {
  .blue::before {
    background-image: linear-gradient(-30deg, #3f4e4f, transparent 50%);
  }
  .blue:nth-child(2n)::before {
    background-image: linear-gradient(30deg, #3f4e4f, transparent 50%);
  }
}

.like_card {
  position: fixed;
  top: 120px;
  right: 25px;
  width: 450px;
  padding: 20px;
  z-index: 3000;
  background-color: #3f4e4f;
  color: #e1e5ea;
}

.like_card h5, p {
  font-size: 16px;
  font-weight: 500;
  text-wrap: wrap;
}

.btn_fav {
  padding: 8px;
  background-color: transparent;
  border: 2px solid #a27b5c;
  color: #d6a278;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  width: 100%;
}

.like_card i {
  font-size: 30px;
  cursor: pointer;
}

.btn_fav:hover {
  background-color: #a27b5c;
  transition: all 0.3s ease-in;
  color: white;
}
