___CSS_LOADER_EXPORT___.push([module.id, `.gallery-title {
    font-size: 40px;
    font-weight: 800;
    color: #3f4e4f;
    text-align: center;
    margin-top: 140px;
}

.btn-filter {
    background-color: transparent;
}

.btn-filter .btn {
    color: #3f4e4f;
    font-weight: 400;
    font-size: 20px;
    transition: font-weight 0.1s, text-decoration 0.1s;
}

.btn-filter .btn:hover {
    font-weight: 700;
    border: transparent;
}

.btn-filter .btn.active {
    border: transparent;
    text-decoration: underline;
    text-underline-offset: 15px;
    font-weight: 700;
}

.gallery-card-title {
    background-color: #A27B5C;
    width: 280px;
    color: #ddd;
    opacity: 0.9;
}

.gallery-card img {
    transition: filter 0.3s ease-in-out;
}

.gallery-card:hover img{
    filter: brightness(50%);
  }

.gallery-card:hover .gallery-card-title {
    font-size: 25px;
    background-color: transparent;
    opacity: 1;
    top: 50%;
    transform: translate(0%, -50%);
    transition: 0.2s;
}
